﻿@import 'Colors.scss';

.imageSlider_slide{
    margin-left: 10px;
}

.slick-prev, .slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}

.slick-next {
    right: -25px;
}

.slick-prev {
    left: -25px;
}

.slick-prev:before {
    content: "<";
    color: $headlineColor;
    font-size: 30px;
}

.slick-next:before {
    content: ">";
    color: $headlineColor;
    font-size: 30px;
}
