﻿@import 'Colors.scss';

.divider {
    width: 100%;
}

hr {
    width: 90%;
    border-style: solid;
    border-color: black;
    display: block;
    unicode-bidi: isolate;
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
    margin-inline-start: auto;
    margin-inline-end: auto;
    overflow: hidden;
    border-width: 1px;
}
